  top: 0;
  left: 0;
  right: 0;
}

nav .company_name {
  float: left;
  color: #fff;
  align-items: center;
  width: 40%;
  height: 100%;
  font-size: 24px;
  display: flex;
}

nav .links {
  float: right;
  justify-content: space-around;
